/* Resets the box size of every element */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Gives the body element a parent height to compare against */
html {
  height: 100%;
}

body {
  min-height: 100%;
  line-height: 1;
  font-family: sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}

/* Matches the font of special elements to the rest of the page */
input, select, option, optgroup, textarea, button,
pre, code {
  font-size: 100%;
  font-family: inherit;
}

/* Removes default bullet points from lists */
ol, ul {
  list-style: none;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #d9dcd6;
  color: #f28047;
}

#mainImage {
  width: 100%
}

.subImg {
  width: 85%
}
.contact-me section{
  text-align: center;
  margin-top: 25px;
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background-color: #f28047;
  color: #d61a17;
}

.header h1{
  display: block;
  font-size: 48px;
  background-color: #d61a17;
  color: #f28047;
}

.header nav {
  text-align: right;
  margin-top: 20px;
}

.header nav ul {
  color: #d61a17;
  list-style: none;
}

.header ul li {
  display: inline-block;
  margin-left: 25px;
}

.header nav ul li a {
  text-decoration: none;
  color: #d61a17;
}

.banner {
  height: 400px;
  width: 100%;
  background-image: url("https://cdn.pixabay.com/photo/2015/04/20/13/17/work-731198_960_720.jpg");
  background-size: cover;
  background-position: center;
  z-index: -1;
  position: relative;
}
.about-me .col-7{
  text-align: justify;
}
.about-me img {
  height: 150px;
  width: 175px;
  float:left;
}

.subtitle {
  position: absolute;
  bottom: 8px;
  right: 16px;
  z-index: 1;
  background-color: #d61a17;
  display: block;
  padding: 0 5px;
  font-size: 25px;
}

.chapter {
  text-align: end;
  border-right: 5px solid #d61a17;
}

.imgblock{
  position: relative;
  padding: 10px;
}

.text-block {
  position: absolute;
  bottom: 12px;
  left: 50px;
  z-index: 1;
  background-color: #d61a17;
  display: block;
  padding: 0 5px;
  opacity: 70%;
}
.nav-bar a {
  background: linear-gradient(to bottom, #d61a17 0%, #d61a17 100%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 4px;
  padding: 8px;
  text-decoration: none;
  transition: background-size 0.2s;
}
.nav-bar a:hover {
  background-size: 4px 50px;
  color: #f28047;
}

.imgblock:hover img {
    opacity: 50%;
}

.contact-me a {
  background: linear-gradient(to bottom, #d61a17 0%, #d61a17 100%);
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 4px;
  padding: 8px;
  text-decoration: none;
  transition: background-size 0.2s;
  color: #f28047;
}
.contact-me a:hover {
  background-size: 4px 50px;
  color: #f28047;
}

@media screen and (max-width: 768px) {
    .chapter {
        border-right: 0px solid #d61a17;
        border-bottom: 5px solid #d61a17;
        text-align: center;
    }
}